body {
  font-family: Arial, Helvetica, sans-serif;
}

.chart {
  border-radius: 8px;
  box-shadow: 9px 14px 36px -6px rgba(0, 0, 0, 0.32);
  margin: 70px auto;
  overflow: hidden;
  width: 80%;
  text-align: center;
}

.container {
  /*background-repeat: no-repeat;
  background-attachment: inherit;
  background-position: center;
  background-size: cover; */
  position: fixed;
  left: 0;
  right: 0;
  z-index: 0;
}

.container:before {
  content: "";
  position: fixed;
  left: 0;
  right: 0;
  z-index: -1;
  background-image: url("./room.jpg"); /*https://alln-extcloud-storage.cisco.com/gblogs/sites/35/2015/06/cisco_mx800.jpg OU https://www.cap-visio.com/wp-content/uploads/2018/11/Cisco-Webex-Room-70S.png*/
  background-repeat: no-repeat;
  background-attachment: inherit;
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 100%;
  filter: blur(5px);
}

.navbar {
  display: flex;
  align-items: center;
  overflow: hidden;
  text-align: center;
  background-color: midnightblue;
  width: 100%;
  color: white;
  font-size: 25px;
}

.img {
  margin-left: 5%;
  margin-right: 1%;
  width: 6%;
}

.inside {
  display: flex;
  justify-content: center;
}

.donut {
  width: 35%;
}

.panel {
  width: 50%;
}

@media screen and (max-width: 1400px) {
  .container {
    position: absolute;
    left: 0;
    right: 0;
    z-index: 0;
  }

  .container:before {
    content: "";
    position: fixed;
    left: 0;
    right: 0;
    z-index: -1;
    background-image: url("./room.jpg"); /*https://alln-extcloud-storage.cisco.com/gblogs/sites/35/2015/06/cisco_mx800.jpg OU https://www.cap-visio.com/wp-content/uploads/2018/11/Cisco-Webex-Room-70S.png*/
    background-repeat: no-repeat;
    background-attachment: inherit;
    background-position: center;
    background-size: cover;
    width: 100%;
    height: 100%;
    filter: blur(5px);
  }
}
