@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(https://fonts.googleapis.com/css?family=Lato:100,300,400,700,900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: Arial, Helvetica, sans-serif;
}

.chart {
  border-radius: 8px;
  box-shadow: 9px 14px 36px -6px rgba(0, 0, 0, 0.32);
  margin: 70px auto;
  overflow: hidden;
  width: 80%;
  text-align: center;
}

.container {
  /*background-repeat: no-repeat;
  background-attachment: inherit;
  background-position: center;
  background-size: cover; */
  position: fixed;
  left: 0;
  right: 0;
  z-index: 0;
}

.container:before {
  content: "";
  position: fixed;
  left: 0;
  right: 0;
  z-index: -1;
  background-image: url(/static/media/room.17df1f72.jpg); /*https://alln-extcloud-storage.cisco.com/gblogs/sites/35/2015/06/cisco_mx800.jpg OU https://www.cap-visio.com/wp-content/uploads/2018/11/Cisco-Webex-Room-70S.png*/
  background-repeat: no-repeat;
  background-attachment: inherit;
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 100%;
  -webkit-filter: blur(5px);
          filter: blur(5px);
}

.navbar {
  display: flex;
  align-items: center;
  overflow: hidden;
  text-align: center;
  background-color: midnightblue;
  width: 100%;
  color: white;
  font-size: 25px;
}

.img {
  margin-left: 5%;
  margin-right: 1%;
  width: 6%;
}

.inside {
  display: flex;
  justify-content: center;
}

.donut {
  width: 35%;
}

.panel {
  width: 50%;
}

@media screen and (max-width: 1400px) {
  .container {
    position: absolute;
    left: 0;
    right: 0;
    z-index: 0;
  }

  .container:before {
    content: "";
    position: fixed;
    left: 0;
    right: 0;
    z-index: -1;
    background-image: url(/static/media/room.17df1f72.jpg); /*https://alln-extcloud-storage.cisco.com/gblogs/sites/35/2015/06/cisco_mx800.jpg OU https://www.cap-visio.com/wp-content/uploads/2018/11/Cisco-Webex-Room-70S.png*/
    background-repeat: no-repeat;
    background-attachment: inherit;
    background-position: center;
    background-size: cover;
    width: 100%;
    height: 100%;
    -webkit-filter: blur(5px);
            filter: blur(5px);
  }
}

.card {
  /* Add shadows to create the "card" effect */
  /* box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2); */
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border-radius: 5px; /* 5px rounded corners */
  padding: 1%;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.container-card {
  display: flex;
  flex-direction: row;
  color: rgb(55, 61, 63);
  justify-content: space-around;
}

/* Add some padding inside the card container */
.container {
  padding: 2px 16px;
}
@media screen and (min-width: 1681px) {
  .flight {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    height: 250px;
    width: 250px;
    border-radius: 50%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  }

  .train {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    height: 250px;
    width: 250px;
    border-radius: 50%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  }

  .car {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    height: 250px;
    width: 250px;
    border-radius: 50%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  }

  h3 {
    font-weight: 500;
    font-size: 150%;
    align-self: center;
    margin: 0%;
  }

  .material-icons {
    margin-top: 8%;
    font-size: 115px;
  }
}

@media screen and (max-width: 1680px) {
  .flight {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    height: 190px;
    width: 190px;
    border-radius: 50%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  }

  .train {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    height: 190px;
    width: 190px;
    border-radius: 50%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  }

  .car {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    height: 190px;
    width: 190px;
    border-radius: 50%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  }

  h3 {
    font-weight: lighter;
    align-self: center;
    margin: 0%;
  }

  .material-icons {
    margin-top: 8%;
    font-size: 95px;
  }
}

body{background-color:#ebf2f6}*,*:before,*:after{box-sizing:border-box}.weather-wrapper{margin-top:20px;display:flex;flex-direction:row;flex-wrap:wrap;justify-content:center}.weather-card{margin:20px 5px;border-radius:20px;position:relative;overflow:hidden;width:270px;height:170px;background-color:#fff;box-shadow:0px 0px 25px 1px rgba(50,50,50,0.1);-webkit-animation:appear 500ms ease-out forwards;animation:appear 500ms ease-out forwards}.weather-card h1{position:absolute;font-family:'Lato', sans-serif;font-weight:300;font-size:80px;color:#B8B8B8;bottom:0;left:35px;opacity:0;-webkit-transform:translateX(150px);transform:translateX(150px);-webkit-animation:title-appear 500ms ease-out 500ms forwards;animation:title-appear 500ms ease-out 500ms forwards}.weather-card p{position:absolute;font-family:'Lato', sans-serif;font-weight:300;font-size:28px;color:#d2d2d2;bottom:0;left:35px;-webkit-animation:title-appear 1s ease-out 500ms forwards;animation:title-appear 1s ease-out 500ms forwards}.weather-icon{position:relative;width:50px;height:50px;top:0;float:right;margin:40px 40px 0 0;-webkit-animation:weather-icon-move 5s ease-in-out infinite;animation:weather-icon-move 5s ease-in-out infinite}.sun{background:#FFCD41;border-radius:50%;box-shadow:rgba(255,255,0,0.1) 0 0 0 4px;-webkit-animation:light 800ms ease-in-out infinite alternate, weather-icon-move 5s ease-in-out infinite;animation:light 800ms ease-in-out infinite alternate, weather-icon-move 5s ease-in-out infinite}@-webkit-keyframes light{from{box-shadow:rgba(255,255,0,0.2) 0 0 0 10px}to{box-shadow:rgba(255,255,0,0.2) 0 0 0 17px}}@keyframes light{from{box-shadow:rgba(255,255,0,0.2) 0 0 0 10px}to{box-shadow:rgba(255,255,0,0.2) 0 0 0 17px}}.cloud-rain{margin-right:60px;background:#b6cede;border-radius:20px;width:25px;height:25px;box-shadow:#b6cede 24px -6px 0 2px,#b6cede 10px 5px 0 5px,#b6cede 30px 5px 0 2px,#b6cede 11px -8px 0 -3px,#b6cede 25px 11px 0 -1px}.cloud-rain:after{content:"";position:absolute;border-radius:10px;background-color:transparent;width:4px;height:12px;left:0;top:31px;-webkit-transform:rotate(30deg);transform:rotate(30deg);-webkit-animation:rain 800ms ease-in-out infinite alternate;animation:rain 800ms ease-in-out infinite alternate}.cloud{margin-right:60px;background:#b6cede;border-radius:20px;width:25px;height:25px;box-shadow:#b6cede 24px -6px 0 2px,#b6cede 10px 5px 0 5px,#b6cede 30px 5px 0 2px,#b6cede 11px -8px 0 -3px,#b6cede 25px 11px 0 -1px}.cloud:after{content:"";position:absolute;border-radius:10px;background-color:transparent;width:4px;height:12px;left:0;top:31px;-webkit-transform:rotate(30deg);transform:rotate(30deg)}.cloud-sun{margin-right:60px;background:#b6cede;border-radius:20px;width:25px;height:25px;box-shadow:#FFCD41 24px -6px 0 2px,#b6cede 10px 5px 0 5px,#b6cede 30px 5px 0 2px,#b6cede 11px -8px 0 -3px,#b6cede 25px 11px 0 -1px}.cloud-sun:after{content:"";position:absolute;border-radius:10px;background-color:transparent;width:4px;height:12px;left:0;top:31px;-webkit-transform:rotate(30deg);transform:rotate(30deg)}.snow{margin-right:60px;background:#b6cede;border-radius:20px;width:25px;height:25px;box-shadow:#b6cede 24px -6px 0 2px,#b6cede 10px 5px 0 5px,#b6cede 30px 5px 0 2px,#b6cede 11px -8px 0 -3px,#b6cede 25px 11px 0 -1px}.snow:after{content:"";position:absolute;border-radius:10px;background-color:transparent;width:6px;height:6px;left:0;top:31px;-webkit-transform:rotate(30deg);transform:rotate(30deg);-webkit-animation:snow 800ms ease-in-out infinite alternate;animation:snow 800ms ease-in-out infinite alternate}@-webkit-keyframes snow{from{box-shadow:#c8cfd6 8px 0px,#c8cfd6 32px -6px,#c8cfd6 20px 0px}to{box-shadow:#c8cfd6 8px 6px,#c8cfd6 32px 0px,#c8cfd6 20px 6px}}@keyframes snow{from{box-shadow:#c8cfd6 8px 0px,#c8cfd6 32px -6px,#c8cfd6 20px 0px}to{box-shadow:#c8cfd6 8px 6px,#c8cfd6 32px 0px,#c8cfd6 20px 6px}}@-webkit-keyframes rain{from{box-shadow:#2092A9 8px 0px,#2092A9 32px -6px,#2092A9 20px 0px}to{box-shadow:#2092A9 8px 6px,#2092A9 32px 0px,#2092A9 20px 6px}}@keyframes rain{from{box-shadow:#2092A9 8px 0px,#2092A9 32px -6px,#2092A9 20px 0px}to{box-shadow:#2092A9 8px 6px,#2092A9 32px 0px,#2092A9 20px 6px}}@-webkit-keyframes weather-icon-move{50%{-webkit-transform:translateY(-8px);transform:translateY(-8px)}}@keyframes weather-icon-move{50%{-webkit-transform:translateY(-8px);transform:translateY(-8px)}}@-webkit-keyframes appear{0%{-webkit-transform:scale(0);transform:scale(0)}50%{-webkit-transform:scale(1.05);transform:scale(1.05)}75%{-webkit-transform:scale(0.95);transform:scale(0.95)}100%{-webkit-transform:scale(1);transform:scale(1)}}@keyframes appear{0%{-webkit-transform:scale(0);transform:scale(0)}50%{-webkit-transform:scale(1.05);transform:scale(1.05)}75%{-webkit-transform:scale(0.95);transform:scale(0.95)}100%{-webkit-transform:scale(1);transform:scale(1)}}@-webkit-keyframes title-appear{from{opacity:0;-webkit-transform:translateX(150px);transform:translateX(150px)}to{opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}}@keyframes title-appear{from{opacity:0;-webkit-transform:translateX(150px);transform:translateX(150px)}to{opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}}

